<template lang="html">
  <div>
    <code-box id="components-table-demo-row-selection" title="" :code="code" :description="description">
      <div slot="instance">
        <ant-table :columns="columns" :data-source="data" :row-selection="rowSelection"></ant-table>
      </div>
    </code-box>
  </div>
</template>

<script>
import { template as description } from './basic.md'

const columns = [{
  title: '',
  dataIndex: 'name',
  render: text => `<a href="#">${text}</a>`
}, {
  title: '',
  dataIndex: 'age'
}, {
  title: '',
  dataIndex: 'address'
}]
const data = [{
  key: '1',
  name: '',
  age: 32,
  address: '1'
}, {
  key: '2',
  name: '',
  age: 42,
  address: '1'
}, {
  key: '3',
  name: '',
  age: 32,
  address: '1'
}]

export default {
  data: () => ({
    description,
    code: `
      <template lang="html">
        <div slot="instance">
          <ant-table :columns="columns" :data-source="data"></ant-table>
        </div>
      </template>

      \<script\>
      const data = [{
        key: '1',
        name: '',
        age: 32,
        address: '1'
      }, {
        key: '2',
        name: '',
        age: 42,
        address: '1'
      }, {
        key: '3',
        name: '',
        age: 32,
        address: '1'
      }]

      export default {
        data: () => ({
          data,
          columns: [{
            title: '',
            dataIndex: 'name',
            render: (text) => \`<a href="#">\${text}</a>\`
          }, {
            title: '',
            dataIndex: 'age'
          }, {
            title: '',
            dataIndex: 'address'
          }, {
            title: '',
            operation: true,
            render: (text, record) => (
              \`<span>
                <a href="#">\${record.name}</a>
                <span className="ant-divider"></span>
                <a href="#"></a>
                <span className="ant-divider"></span>
                <a href="#" className="ant-dropdown-link">
                   <ant-icon type="down"></ant-icon>
                </a>
              </span>\`
            )
          }]
        })
      }
      \</script\>
    `,
    data,
    columns,
    rowSelection: {
      onChange (selectedRowKeys, selectedRows) {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows)
      },
      onSelect (record, selected, selectedRows) {
        console.log(record, selected, selectedRows)
      },
      onSelectAll (selected, selectedRows, changeRows) {
        console.log(selected, selectedRows, changeRows)
      }
    }
  })
}
</script>



